/* src/components/Dice.css */
.dice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  position: relative;
}

.die {
  position: relative;
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  transform: rotateX(-30deg) rotateY(-30deg);
  transition: transform 2s;
}

.die.rolling {
  animation: roll 2s infinite;
}

@keyframes roll {
  0% { transform: rotateX(0deg) rotateY(0deg); }
  100% { transform: rotateX(360deg) rotateY(360deg); }
}

.face {
  position: absolute;
  width: 0;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-bottom: 43.3px solid #ffcc00; /* Height of an equilateral triangle */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  transform-style: preserve-3d;
}

.d20 .face-1 { transform: rotateY(0deg) rotateX(58.3deg) translateZ(50px); }
.d20 .face-2 { transform: rotateY(72deg) rotateX(58.3deg) translateZ(50px); }
.d20 .face-3 { transform: rotateY(144deg) rotateX(58.3deg) translateZ(50px); }
.d20 .face-4 { transform: rotateY(216deg) rotateX(58.3deg) translateZ(50px); }
.d20 .face-5 { transform: rotateY(288deg) rotateX(58.3deg) translateZ(50px); }
.d20 .face-6 { transform: rotateY(0deg) rotateX(-58.3deg) translateZ(50px); }
.d20 .face-7 { transform: rotateY(72deg) rotateX(-58.3deg) translateZ(50px); }
.d20 .face-8 { transform: rotateY(144deg) rotateX(-58.3deg) translateZ(50px); }
.d20 .face-9 { transform: rotateY(216deg) rotateX(-58.3deg) translateZ(50px); }
.d20 .face-10 { transform: rotateY(288deg) rotateX(-58.3deg) translateZ(50px); }
.d20 .face-11 { transform: rotateY(36deg) rotateX(31.7deg) translateZ(50px); }
.d20 .face-12 { transform: rotateY(108deg) rotateX(31.7deg) translateZ(50px); }
.d20 .face-13 { transform: rotateY(180deg) rotateX(31.7deg) translateZ(50px); }
.d20 .face-14 { transform: rotateY(252deg) rotateX(31.7deg) translateZ(50px); }
.d20 .face-15 { transform: rotateY(324deg) rotateX(31.7deg) translateZ(50px); }
.d20 .face-16 { transform: rotateY(36deg) rotateX(-31.7deg) translateZ(50px); }
