/* src/components/BossFight.css */
.boss-fight {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  overflow-y: auto;
  margin: 20px;
}

.boss-section {
  text-align: center;
  margin-bottom: 20px;
}

.boss-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

.health-bar-container {
  width: 100%;
  background-color: #ccc;
  border-radius: 10px;
  margin: 10px 0;
  overflow: hidden;
}

.health-bar {
  height: 20px;
  background-color: #76c7c0;
  border-radius: 10px;
  transition: width 0.5s ease-in-out;
}

.error {
  color: red;
}

.chores-list,
.history-list {
  list-style-type: none;
  padding: 0;
  width: 100%;
}

.chore-item {
  background: #444;
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  margin-left: 10px;
  background-color: #3498db;
  border: none;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #2980b9;
}

.highlight {
  background-color: #f39c12;
}

.dice-container {
  margin-top: 20px;
}
